


















import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'

@Component({})
export default class AppIcon extends Vue {
  @Prop({ type: String, default: 'success' })
  width!: string;

  @Prop({ type: String, default: '40' })
  height!: string;

  @Prop({ type: String, required: true })
  primaryColor!: string;

  @Prop({ type: String, required: true })
  secondaryColor!: string;
}
